// variables
$c24: #1B1C33;
$white: #fff;
$gray:#5F5F5F;
$c455:#455582;
$f64:64px;
$f24: 24px;
//customize classes
.w-700{font-weight: 700;font-style: normal}
.w-400{font-weight: 400;font-style: normal;}
.w-600{font-weight: 600;font-style: normal;}
.font-open-sans{
font-family: 'Open Sans';
@extend .w-700;
}
.font-playfair{
font-family: 'Playfair Display';
@extend .w-700;

}

//page classess
.plan{
width: 604px;
height: 865px;
left: 97px;
top: 267px;
margin: 30px;
background: $white;
border: 1px solid $c455;
border-radius: 25px;
padding: 50px 100px;

@media (max-width:1102px) {
    height: unset;
    padding: 80px;
    @media (max-width:986px) {
        margin: 20px auto;
        @media (max-width:680px) {
            width: 90%;
            margin: 30px auto;
            @media (max-width:420px) {
                padding: 10px;
            }
            
        }
    }
}
.head{
    font-size: $f24;
    line-height: 33px;
    // color: $c24;
    @extend .font-open-sans;
    @media (max-width:511px) {
        line-height: 20px;
        @media (max-width:425px) {
            text-align: center;
        }
    }
    }
    .title{
    font-size: 64px;
    line-height: 85px;
    // color: $c24;
    @extend .font-playfair;
    @media (max-width:511px) {
        font-size: 40px;
        line-height: 50px;
        @media (max-width:425px) {
            text-align: center;
        }
    }
    }
    .subtitle{
    font-size: $f24;
    line-height: 33px;
    // color:$c24;
    @media (max-width:511px) {
        font-size:$f24 ;
        line-height: 20px;
        @media (max-width:425px) {
            text-align: center;
        }
    }
    }
    .checkboxDiv{
        display: flex;
        margin: 40px 0;
       
        .img{
            transform: scale(0.8);
            margin-left: -60px;
            height: 32px;
            @media (max-width:680px) {
                margin-left: -10px;
                @media (max-width:420px) {
                    margin-left: 10px;
                }
            }
        }
        .checkText{
            // color:$gray;
            font-size: 16px;
            line-height: 22px;
            margin-top: 4px;
            margin-left: 20px;
            
            @extend .font-open-sans;
            @extend .w-400;
        }
        
    }
    
    .btn{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 10px;
        gap: 10px;
        width: 310px;
        height: 54px;
        margin: 80px auto;
        background: #1B1C33;
        border-radius: 8px;
       @media (max-width:1440px) {
           margin: unset;
        @media (max-width:1102px) {
            width: 100%;
            height: 100%;
            margin: 45px auto;
            @media (max-width:465px) {
                width: 80%;
                margin: 10px auto;
                text-align: center;
            }
        }
       }
        .btntext{
            font-size: 16px;
            line-height: 22px;
            color: $white;
            @extend .font-open-sans;
            @extend .w-600;
            
        }
    }
}
