.font-opem-sans{
    font-family: 'Open Sans';
    font-style: normal;

}

.sidebar{
    width:280px;
    height: 100vh;
    transition: 0.8s all ease;
    background-color: #455582;
    padding: 20px 0px 20px 50px;
    ul>li{
        display: block;
        margin: 40px 0;
        transition: 0.3s all ease;
        &>a{
            color: #CCD5E7;
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            @extend .font-opem-sans;
            @media (max-width:810px) {
            font-size: 12px;
            text-align: center;
            display: flex;
            justify-content: center;
            }
        }
    }
    .active{
        background: #FFFFFF;
        padding: 20px 10px 20px 20px;
        border-radius: 50px 0px 0px 50px;
        transform: translateX(-3px);
        width: 104%;
        & >a{
            color: #455582;
            @media (max-width:810px) {
            text-align: center;
            }
        }
        @media (max-width:810px) {
            transform: translateX(0);
            border-radius: 5%;
            margin: 2px;
            padding: 10px;
        }
    }
    @media (max-width:1150px) {
        width: 100px;
        padding: 0;
        img{
            transform: scale(0.7);
        }
        &:hover{
            width: 100%;
        }
    }
}