.home{
    .custom-unique-font{
        font-family: 'Noto Serif JP', serif;
        font-weight: 900;
        font-size: 22px;
        color: rgb(0, 0, 0) !important;
        span{
            font-family: 'Dancing Script', cursive!important;
            font-size: 35px;
            font-weight: 700;
        }
    }
    .password {
        margin-bottom: 54px;
    }
    
    a {
        color: inherit !important;
        font-family: inherit !important;
        font-size: inherit !important;
        font-weight: inherit !important;
        
    }
    
    .label-text {
        position: absolute;
        pointer-events: none;
        line-height: 1;
        transition: 0.2s ease-out;
        top: 50%;
        transform: translateY(-40%);
        background: #fff;
        left: 90px;
        padding: 5px 10px;
        border-radius: 100vh;
        font-size: 16px;
        color: #1b1c33;
    }
    
    .label-text[data-stay-up="true"] {
        top: 0;
        transform: translate(0);
        font-size: 12px;
    }
}
#root {
    max-width: unset !important;
    min-width: unset !important;
}
@media only screen and (max-width: 410px) {
    .home{
        .custom-unique-font{
            font-family: 'Noto Serif JP', serif;
            font-weight: 900;
            font-size: 16px;
            color: rgb(0, 0, 0) !important;
            span{
                font-family: 'Dancing Script', cursive!important;
                font-size: 30px;
                font-weight: 700;
            }
        }
    }
  }