.font-open-sans{
    font-family: 'Open Sans';
    font-style: normal;
}
.font-playfair{
    font-family: 'Playfair Display';
    font-style: normal;
}
.ltr{
    width: 80%;
    margin: 10px auto;
    background: #CCD5E7;
    padding: 60px 50px;
    display: flex;
    justify-content: space-between;
    @media (max-width:1580px) {
        width: 90%;
        @media (max-width:1401px) {
            padding: 60px 10px;
            @media (max-width:1162px) {
                flex-direction: row-reverse;
                @media (max-width:1096px) {
                    flex-direction: column-reverse;
                }
            }
        }
    }
    .container{
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        padding: 20px;
        position: relative;
        gap: 10px;
        width: 538px;
        height: 673px;
        background: #FFFFFF;
        @media (max-width:1331px) {
            width: 40% !important;
            height: 608px;

            @media (max-width:1096px) {
                width: 75% !important;
            height: auto;
            margin: auto;
            }
            
        }
        .img1{
            @media (max-width:1331px) {
                width: 100%;
                height: 100%;
                margin: auto;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
        .img2{
            width: 160px;
            height: 203px;
            position: absolute;
            bottom: -90px;
            right: -15px;
            @media (max-width:644px) {
                display: none;
            }
        }
        }
        @media (max-width:1580px) {
            width: unset;
           
    }
    .section{
        width: 583px;
        @media (max-width:1096px) {
            width: 80%;
            margin: 20px auto;

            @media (max-width:620px) {
                width: 96%;
            }

        }
        .title{
            @extend .font-open-sans;
            font-weight: 700;
            font-size: 24px;
            line-height: 33px;
            margin-top: -20px;
            margin-bottom: 40px;
            color: #1B1C33;
            
        }
        .head{
        @extend .font-playfair;
        font-weight: 700;
        font-size: 64px;
        line-height: 85px;
        color: #1B1C33;
        margin: 40px 0;
        @media (max-width:520px) {
            font-size: 50px;
            line-height:50px;
        }
        }
        .para1{
            @extend .font-open-sans;
            font-size: 24px;
            line-height: 33px;
            font-weight:700;
            color: #1B1C33;
        }
        .para2{
            @extend .font-open-sans;
            font-size: 24px;
            line-height: 33px;
            color: #1B1C33;
            font-weight:700;
            margin: 20px 0;
        }
        button{
            outline:none;
            background:#1B1C33;
            border: none;
            padding:15px 88px;
            display: flex;
            margin:50px 0;
            color:#fff;
            border-radius: 8px;
            @media (max-width:600px) {
                margin: 10px auto;
            }
            @media (max-width:526px) {
                width: 100%;
                justify-content: center;
            }
          .register{
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            color: #FFFFFF;
            @extend .font-open-sans
            
          }
    }
    }
}