.txt{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #455582;
}

.status{
width: 422px;
height: 388px;
background: #CCD5E7;
padding: 20px;
border-radius: 24px;
margin: 80px 20px 20px 20px;
.container{
    position: relative;
    width: 242px;
    margin:50px;

    .circle_1{
        width: 242px;
        height: 242px;    
        border-radius:50%;
        border: 20px solid;
        border-color: #EB5757 #EB5757  #219653  #219653;
        transform: rotate(-43deg);
        -webkit-mask: radial-gradient(transparent 89px,#000 90px);
                mask: radial-gradient(transparent 89px,#000 90px);
        // border-radius: 50%;
        // position:absolute;
        // background: linear-gradient(#EB5757,green);
    }
    .text{
    position: absolute;
    top: 110px;
    left: 70px;
    @extend .txt
    }
    .text2{
        text-align: center;
        margin: 20px 0;
        @extend .txt
    }
}
}