
.flex{
    display: flex;
    justify-content: space-between;
}
.font-open-sans{
    font-family: 'Open Sans';
    font-style: normal;
}
.billing{
    width:748px;
    height: 660px;
    background: #FFFFFF;
    box-shadow: 0px 0px 100px rgba(0, 0, 0, 0.05);
    border-radius: 8px;
    padding: 50px 100px;
    margin: 60px auto 100px auto;
    .firstSection{
        width: 90%;
        margin: 0 auto;
        margin-bottom: 50px;
        @extend .flex;
        
        .title{
            font-weight: 700;
            font-size: 24px;
            line-height: 33px;
            color: #455582;
            margin: 20px 0;
            @extend .font-open-sans;
            @media (max-width:590px) {
                font-size: 18px;
                @media (max-width:380px) {
                    margin: 0;
                    font-size: 20px;
                }
            }
        }
        img{
            border: 1px solid #455582;
            border-radius: 8px;
            @media (max-width:670px) {
            transform: scale(0.8);
            @media (max-width:462px) {
                transform: scale(0.6);
                @media (max-width:380px) {
                    transform: scale(0.5);
                }
            }
            &.img{
                position: relative;
                right: 33px;
            }
            @media (max-width:562px) {
                &.img2{
                    position: relative;
                    left: -27px;
                  
                }
            }
            
            }
        }
    }
    form{
        .form-control{
            width: 100%;
            margin: 20px 0;
            label{
                font-weight: 600;
                font-size: 16px;
                line-height: 22px;
                color: #292929;
                @extend .font-open-sans
            }
            input{
                background: #FFFFFF;
                border: 1px solid #969595;
                border-radius: 5px;
                outline: none;
                height: 64px;
                padding:10px 10px 10px 20px;
                width: 100%;
            }
            
        }
        .diff{
            width: 100%;
            @extend .flex;
            .col_1{
                width:100% ;
                margin-right:20px
            }
            .col_2{
                width: 80%;
            }
        }
        button{
            border: none;
            outline: none;
            background: #1B1C33;
            border-radius: 5px;
            padding: 10px;
            width: 358px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 40px auto;
            height: 64px;
            @media (max-width:418px) {
                width: 80%;
            }
            .text{
                font-weight: 700;
                font-size: 24px;
                line-height: 33px;
                color:#FFFFFF;
                @extend .font-open-sans;
                @media (max-width:318px) {
                    font-size: 20px;
                }
               
            }
        }
    }
    @media (max-width:783px) {
        width: 90%;
        padding: 50px;
        .content{
            width: 50%;
        }
        @media (max-width:590px) {
            padding: 12px;
        }
        
    }
}

