.myDashboard{
    width: calc(100vw - 288px);
    .stat{
        margin: 20px auto;
        width: calc(100vw - 40%);
        display: flex;
        justify-content: space-between;
    }
    .video{
        margin: 20px auto;
        width: calc(100vw - 30%);
        .title{
            font-family: 'Open Sans';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            margin-left: 20px;
            color: #1B1C33;

        }
        .flex{
            display: flex;
            width: calc(100vw - 32%);
            justify-content: space-between;
           
            .div1,.div2{
                width: 498px;
                background-color: red;
                margin-top: 10px; padding: 10px;
                background-color: #CCD5E7;
                height: 288px;
                border-radius: 5px;
            }
        }
    }
}