.network{
    width: 100%;
    height: auto;
    left: 0px;
    top: 0px;
    background: #455582;
    img{
        width: 80%;
        display: flex;
        margin: auto;
        justify-content: center;
    }
    .title{
        font-family: 'Playfair Display';
        font-style: normal;
        font-weight: 700;
        font-size: 96px;
        line-height: 128px;
        color: #CCD5E7;
        text-align: center;
        @media (max-width:746px) {
            font-size: 64px;
            line-height: 95px;
            @media (max-width:500px) {
                font-size: 40px;
                line-height: 60px;
            }
        }

    }
}