


.sidedrawer{
    width: 70% !important;
    height: 100vh;
    background-color: #fff;
    position: fixed;
    top:0;
    left:0;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    z-index:200;
    transform: translateX(-100%);
    transition: all 0.3s ease-out;
    display: flex;
    flex-direction: column ;
    justify-content: center;
}
.sidedrawer.show{
    transform: translateX(0);
}

.sidedrawer___logo{
    position: absolute;
    left:16%;
    top: 0;
    transform: scale(0.6);
    a{
        display: flex;
        justify-content: center;
    }
}
.sidedrawer__links{
    display:flex;
    flex-direction:column;
    // margin-top: -80px;
    list-style: none;

}
.sidedrawer__links>li{
    display:flex;
    align-items: center;
}
.sidedrawer__links>li>a,.sidedrawer__links>li>.dropdown{
    display:flex;
    align-items: center;
    justify-content: center;
    flex:1;
    padding:1rem;
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 24px !important;
    line-height: 28px;
    display: flex;
    align-items: center;
    text-align: center;
}

.sidedrawer__links .popcontainer{
    box-shadow: 1px 1px 5px #000;
    top: -70px;
    left: -220px !important;   
    white-space: pre-wrap;
    height: 500px;
    overflow-y:auto
}
.sidedrawer__links .dropdown-menu{
    margin-top: 140px !important;
}
.sidedrawer__links>li>.dropdown>button{
    font-size:24px
}

.sidedrawer__links>li>a:hover,.sidedrawer__links>li>.dropdown:hover
{
    background: #ccd5e786;
    color:#f4f4f4
}

.sidedrawer__links>li>a>span{
    display:flex;
    align-items:center;
    margin-left: 8px;
}

/* countering bootstrap */
button:focus:not(:focus-visible) {outline: none;border:1px solid transparent}
.sidedrawer .accordion-button:focus {
    border-color: transparent;
    box-shadow:none;
    color: #760000;
}

.sidedrawer .accordion-button::after {
    margin-left: 20px;
    margin-right: -20px !important;
}
.hamburger-react{
    z-index: 10000000000;
}
.sidedrawer .accordion{
    margin: auto ;
}
.sidedrawer button:not(.collapsed)::after {
    margin-top: -10px;
    margin-right: -20px !important;
}
.sidedrawer .accordion-button::after {
    margin-top: 5px;
}
.accordion-button:not(.collapsed):after {
  color: green;
}
.sidedrawer .accordion-item{
    border: none ;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 24px !important;
    line-height: 28px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #760000;
}
.sidedrawer .accordion-button:not(.collapsed) {
    color: inherit;
    background-color: transparent;
    box-shadow: unset;
}
.sidedrawer .accordion-button{
    display: flex !important;
    align-items: center !important;
    justify-content: center;
    flex: 1 1;
    padding: 1rem;
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 24px !important;
    line-height: 28px;
    display: flex;
    width:unset;
    align-items: center;
    text-align: center;
    color: #760000;
}
.sidedrawer .accordion-button:hover{
    color: #760000;
}
.sidedrawer .accordion-body{
    font-size:15px !important;
    padding:unset
}

