.font-open-sans{
    font-family: 'Open Sans';
    font-style: normal;
}

.quote{
    width: 1029px;
    height: 363px;
    margin: 30px auto;
    background: #FBFBFB;
    border: 1px solid #BDBDBD;
    border-radius: 4px;
    .title{
        @extend .font-open-sans;
        font-weight: 700;
        font-size: 24px;
        line-height: 33px;
        padding:20px 20px 40px 20px ;
        border: 1px solid #BDBDBD;
        color: #212121;
    }
    .detail{
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        display: flex;
        justify-content: center;
        // width: 1029px;
        height: 193px;
        align-items: center;
        color: #BDBDBD;
        @extend .font-open-sans
    }
    @media (max-width:1120px) {
        width: 80%;
    }
}