.navbar{
    
    width: 100%;
    margin-top: 50px;
    // padding: 0 20px 0 0;
    height: 86px;
    .section{
        display: flex;
        background: #CCD5E7;
        justify-content: space-between;
        align-items: center;
        padding: 16px 32px;
        gap: 10px;

        .title{
            font-family: 'Open Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 33px;
            color: #455582;


        }
        .icon{
            margin-right: 40px;
            img{cursor: pointer;
                margin: 0 10px;
            }
        }
    }
    .body{
        display: flex;
        justify-content: space-between;
        padding: 30px;
        button{
            background: transparent;
            outline: none;
            border: none;
            font-family: 'Open Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 22px;
            color: #212121;
        }
        .welcome{
            font-family: 'Playfair Display';
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            line-height: 27px;
            color: #455582;
        }
    }
}