.font-open-sans{
    font-family: 'Open Sans';
    font-style: normal;
}
.font-playfair{
    font-family: 'Playfair Display';
    font-style: normal;
}
.rtl{
    width: 80%;
    margin: 10px auto;
    background: #CCD5E7;
    padding: 60px 50px;
    display: flex;
    justify-content: space-between;
    @media (max-width:1560px) {
        width: 90%;
        @media (max-width:1385px) {
            padding: 20px;
            @media (max-width:1096px) {
                flex-direction: column  ;
            }
        }
    }
    .section{
        width: 583px;
        @media (max-width:1385px) {
            margin-right: 40px;
            @media (max-width:1096px) {
                width: 80%;  
                margin: 10px auto;
                @media (max-width:520px) {
                    width: 100% !important;
                    height: auto;
                    margin: auto;
                }
            }
         }
        .head{
        @extend .font-playfair;
        font-weight: 700;
        font-size: 64px;
        line-height: 85px;
        color: #1B1C33;
        margin: 40px 0;
        @media (max-width:1096px) {
            text-align: center;
            @media(max-width:520px){
                font-size: 50px;
                line-height: 50px;
            }
        }
        
        }
        .para{
            @extend .font-open-sans;
            font-size: 24px;
            line-height: 33px;
            font-weight:700;
            color: #1B1C33;
        }
        .para2{
            @extend .font-open-sans;
            font-size: 24px;
            line-height: 33px;
            font-weight:700;
            color: #1B1C33;
            margin: 40px 0;
        }
        button{
            outline:none;
            background:#1B1C33;
            border: none;
            padding:15px 88px;
            display: flex;
            margin:50px 0;
            color:#fff;
            @media (max-width:1096px) {
                width: 80%;  
                margin: 20px auto;
                justify-content: center;
            }
            border-radius: 8px;
          .register{
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            color: #FFFFFF;
            @extend .font-open-sans
            
          }
    }
    }
    .container{
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        padding: 20px;
        position: relative;
        gap: 10px;
        width: 538px;
        height: 673px;
        background: #FFFFFF;
        @media (max-width:1225px) {
            width: 40% !important;
            height: 100% !important;
            @media (max-width:1096px) {
                width: 100% !important;  
                margin: 10px auto;
            }
            .img,img{
                width: 100%;
            }
        }
    }
}