.font-open-sans{
    font-family: 'Open Sans';
    font-style: normal;
}
.utils-16{
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
}
.table{
    table{
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
    }
    overflow-x: auto;
    border-collapse: collapse;
    width: 1023px;
    margin: 30px  auto;
    @media (max-width:1047px) {
        width: 95%;
    }
    tr{
        
        &:first-child{
            width: 1023px;
            margin: 40px;
            height: 64px;
            background: #455582;
            th{
                color: #FFFFFF;
                @extend .utils-16;
                @extend .font-open-sans
            }
           
        }
        &:nth-child(2){
            margin: 10px 0;
        }
        td{
            color: #1B1C33;
            padding: 20px;
            max-width: 148px;
            height: 114px;
            background: #FBFBFB;
            text-align: center;
            &:nth-child(3){
                color: #219653;
            }
            &::before{
                height: 1em;
                display: table-row;
                content: '';
            }
            @extend .utils-16;
            @extend .font-open-sans;
        }
    }
}