.flex{
    display: flex;
    justify-content: center;
}

.portal2{
.main{
width: 1300px;
margin: 30px auto;
@extend .flex;

@media (max-width:1400px) {
    width: 90%;
    @media (max-width:1102px) {
        width: 100%;
        @media (max-width:986px) {
            flex-direction: column;
        }
    }
}
}
}