        }
    }

.portal{
    min-height: 100vw;
    // overflow-y: hidden;
    .header_i8jr6{
        @media (max-width:1050px) {
            padding: 010px 20px!important;
        }
    }
    .bg_1gty6x8{
        @media (max-width:1050px) {
            padding: 0 !important;
        }
    }

    .div{
        width: 1400px;
        margin: 30px auto;
        padding: 20px;
        display: flex;
        justify-content: space-between;
        @media (max-width:1408px) {
            width: 100%;
            @media (max-width:1056px) {
                display: block;
            }
        }
        .div_1{
            width: 602px;
            height: 462px;
            background: #FFFFFF;
            box-shadow: 0px 2px 37px rgba(0, 0, 0, 0.1);
            border-radius: 8px;
            padding: 30px;
            img{
                width: 49%;
            }
            @media (max-width:1208px) {
                width: 60%;
                @media (max-width:1056px) {
                    width: 80%;
                    margin: 10px auto;
                    @media (max-width:500px) {
                       width: 100%;
                    }
                }
            }
            img{
                display: flex;
                justify-content: center;
                align-items: center;
                width: 20%;
                margin: 10px auto;
                
            }
            .detail{
                width: 475px;
                height: 66px;
                margin: 50px auto;
                font-weight: 700;
                font-size: 24px;
                line-height: 33px;
                text-align: center;
                color: #455582;
                @extend .font-open-sans;
                @media (max-width:730px) {
                    width: 80%;
                }
                
            }
            button{
                    outline:none;
                    background:#1B1C33;
                    border: none;
                    padding:15px 88px;
                    width: 300px;

                    display: flex;
                    margin:50px auto;
                    justify-content: center;
                    color:#fff;
                    border-radius: 8px;
                    @media (max-width:626px) {
                        margin-top: 100px;
                        width: 100%;
                        text-align: center;
                        padding: 15px 53px;

                        @media (max-width:504px) {
                            margin-top: 120px;
                            text-align: center;
                        }
                    }
                  .register{
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 22px;
                    color: #FFFFFF;
                    @extend .font-open-sans;
                  
                    
                  }
            }
        }
        .div_2>img{
            @media(max-width:1208px) {
              transform: scale(0.7);
              margin-left: -80px;
              @media (max-width:1056px) {
                transform: scale(1);
                display: flex;
                justify-content: center;
                margin: auto;
                @media (max-width:846px) {
                    width: 80%;
                }
            }
            }
        }
    }
